.userOrders {
    background-color: #f2f2f2;
}
.userOrders-container {
    max-width: 1200px;
    margin: 0 auto;
    padding-top: 2rem;
}
.userNav-menu {
    background-color: #fff;
    display: flex;
    flex-direction: column;
    border-radius: 10px;
    height: 300px;
    width: 200px;
}
.userNav-menu h5 {
    padding: 0.2rem 1rem;
}
.userNav-menu a {
    padding: 0.2rem 1rem;
    color: #333;
}
.selected-nav {
    border-left: 3px solid #f60;
    color: #f60;
    background-color: #f2f2f2;
}
.updatePassword-form {
    padding: 3rem;
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
}
.updatePassword-form label {
    padding: 0.2rem 1rem;
    border-radius: 5px;
}
.updatePassword-form input {
    padding: 0.2rem 1rem;
    border-radius: 5px;
    height: 40px;
    width: 250px;
}
.password-icon {
    transform: translateX(-1.5rem);
    cursor: pointer;
}
.btn-updatePass {
    background-image: linear-gradient(94deg, #ff0a0a, #ff7539);
    padding: 0.6rem 2rem;
    border-radius: 5px;
    color: #fff;
    font-weight: 600;
    width: 150px;
}
